* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.container {
  max-width: 1280px;
  margin: auto; }

@media (max-width: 1199px) {
  .container {
    max-width: 960px; } }
@media (max-width: 991px) {
  .container {
    max-width: 750px; } }
@media (max-width: 768px) {
  .container {
    max-width: 100%;
    padding: 0 20px; } }
@media (max-width: 576px) {
  .container {
    width: 100%; } }
